@media only screen and (min-width: 320px) {
  .header-main-div {
    box-sizing: border-box;
    width: 320px;
    z-index: 1000;
    height: 64px;
    background: #fff;
    border: 1.5px solid #111;
    border-radius: 0 0 8px 8px;
    justify-content: flex-start;
    align-items: center;
    gap: 70px;
    margin: 0% auto;
    display: flex;
    position: relative;
  }

  .header-user-part-div {
    width: 82px;
    justify-content: center;
    align-items: center;
    gap: 24px;
    display: flex;
  }

  .header-mobile-menu-icons {
    width: 40px;
    width: 40px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
  }

  .burger-menu, .header-cross {
    display: block;
    position: absolute;
  }

  .hidden, .header-navigation, .user-log {
    display: none;
  }

  .header-part-div {
    display: flex;
  }

  .header-logo {
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-left: 20px;
    display: flex;
  }

  .switch {
    margin-left: auto;
    display: flex;
  }

  .switch input {
    width: 40px;
    height: 20px;
    cursor: pointer;
    -ms-appearance: none;
    appearance: none;
    transition: all .5s;
    background: #7564be;
    border-radius: 20px;
    outline: none;
    -webkit-transition: all .4s;
    position: relative;
    box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
  }

  .switch input:checked {
    background-color: #32285d;
    box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
  }

  .switch input:before {
    content: "";
    width: 16px;
    height: 16px;
    background: var(--white-color);
    border-radius: 20px;
    transition: all .5s;
    position: absolute;
    top: 2px;
    left: 2px;
    transform: scale(1.1);
    box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
  }

  .switch input:checked:before {
    left: 22px;
  }
}

